<script>
import axios from "axios";
import FFooter from "../components/FFooter.vue";
import landing from "../components/landing.vue";
import slider from "../components/slider.vue";
import squares from "../components/squares.vue";
// import monitor from "../components/monitor.vue";

export default {
  name: "Home",
  components: {
    landing,
    FFooter,
    slider,
    squares,
    // monitor,
  },
  data() {
    return {
      contactForm: {
        name: "",
        email: "",
        phone: "",
        message: "",
        service: "",
      },
      screens: [
        require("../assets/screens/social.png"),
        require("../assets/screens/interior.png"),
        require("../assets/screens/interior.png"),
        require("../assets/screens/interior.png"),
      ],
      deletedScreens: [],
    };
  },
  methods: {
    async sendMessage() {
      let request = await axios.post(
        `${this.backend}/messages`,
        this.contactForm
      );
      let result = request.data;
      if (result.status == 200) {
        alert("message sent succesfully");
      } else {
        alert("message couldn't be sent");
      }
      console.log(this.contactForm);
    },
  },
  mounted() {
    setInterval(() => {
      if (this.screens.length > 1) {
        let lastScreen = this.screens.pop();
        this.deletedScreens.push(lastScreen);
      } else {
        this.screens = [...this.deletedScreens, this.screens[0]];
        this.deletedScreens = [];
      }
    }, 2000);
  },
};
</script>
